.card-nav-container {
    position: fixed; /* 확대/축소에도 고정 */
    top: 32px; /* px 단위로 고정 */
    left: 50%;
    transform: translateX(-50%);
    width: 800px; /* px 단위로 고정 */
    max-width: 95vw; /* 화면이 작을 때만 반응형 */
    z-index: 99;
    box-sizing: border-box;
}

.card-nav {
    display: block;
    height: 76px; /* 기본 상태에서 하단 공간 확보 */
    padding: 0; /* 패딩 제거 */
    background-color: white;
    border: 0.5px solid rgba(255, 255, 255, 0.1);
    border-radius: 0.75rem;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    position: relative;
    overflow: visible; /* 둥근 모서리/그림자 잘림 방지 */
    will-change: height;
}

.card-nav.open {
    overflow: hidden; /* 메뉴 펼침 시에만 hidden */
}

.card-nav-top {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0.5rem 0.45rem 0.55rem 1.1rem;
    z-index: 2;
}

.hamburger-menu {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    gap: 6px;
}

.hamburger-menu:hover .hamburger-line {
    opacity: 0.75;
}

.hamburger-line {
    width: 30px;
    height: 2px;
    background-color: currentColor;
    transition:
            transform 0.25s ease,
            opacity 0.2s ease,
            margin 0.3s ease;
    transform-origin: 50% 50%;
}

.hamburger-menu.open .hamburger-line:first-child {
    transform: translateY(4px) rotate(45deg);
}

.hamburger-menu.open .hamburger-line:last-child {
    transform: translateY(-4px) rotate(-45deg);
}

.logo-container {
    display: flex;
    align-items: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.logo {
    height: 28px;
}

.card-nav-cta-button {
    background-color: #111;
    color: white;
    border: none;
    border-radius: calc(0.75rem - 0.35rem);
    padding: 0 1rem;
    height: 100%;
    font-weight: 500;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.card-nav-cta-button:hover {
    background-color: #333;
}

.card-nav-content {
    position: absolute;
    left: 0;
    right: 0;
    top: 70px; /* 상단 바와 겹치지 않게 top 값 증가 */
    min-height: 120px;
    height: auto;
    padding: 1rem 0.5rem 1.5rem 0.5rem; /* padding-top 추가 */
    display: flex;
    align-items: flex-end;
    gap: 12px;
    visibility: hidden;
    pointer-events: none;
    z-index: 2; /* z-index 증가 */
    overflow-y: auto;
}

.card-nav.open .card-nav-content {
    visibility: visible;
    pointer-events: auto;
}

.nav-card {
    height: 100%;
    flex: 1 1 0;
    min-width: 0;
    border-radius: calc(0.75rem - 0.2rem);
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 12px 16px;
    gap: 8px;
    user-select: none;
}

.nav-card-label {
    font-weight: 400;
    font-size: 22px;
    letter-spacing: -0.5px;
}

.nav-card-links {
    margin-top: auto;
    display: flex;
    flex-direction: column;
    gap: 2px;
}

.nav-card-link {
    font-size: 16px;
    cursor: pointer;
    text-decoration: none;
    transition: opacity 0.3s ease;
    display: inline-flex;
    align-items: center;
    gap: 6px;
    color: inherit; /* 부모 카드의 textColor를 상속받음 (흰색 유지) */
}

.nav-card-link:hover,
.nav-card-link:active,
.nav-card-link:focus {
    opacity: 0.75;
    color: inherit; /* hover 등에서도 색상 유지 */
}

.card-nav.open .nav-card {
    opacity: 1 !important;
    transform: none !important;
}

@media (max-width: 900px) {
    .card-nav-container {
        width: 95vw;
        max-width: 95vw;
        top: 16px;
    }

    .card-nav-top {
        padding: 0.5rem 1rem;
        justify-content: space-between;
    }

    .hamburger-menu {
        order: 2;
    }

    .logo-container {
        position: static;
        transform: none;
        order: 1;
    }

    .card-nav-cta-button {
        display: none;
    }

    .card-nav-content {
        top: 70px;
        padding: 1rem 0.5rem 1.5rem 0.5rem;
        flex-direction: column;
        align-items: stretch;
        gap: 8px;
        padding: 0.5rem 0.5rem 1.5rem 0.5rem;
        bottom: 0;
        justify-content: flex-start;
        min-height: 120px;
        height: auto;
        overflow-y: auto;
    }

    .nav-card {
        height: auto;
        min-height: 60px;
        flex: 1 1 auto;
        max-height: none;
    }

    .nav-card-label {
        font-size: 18px;
    }

    .nav-card-link {
        font-size: 15px;
    }
}
